

/* Text Block
======================================================================= */
.elemental-area--SidebarArea .textblock {
	font-size: rem-calc(16);
}

/* Textblock
======================================================================= */
.elemental-area--SidebarArea .textblock {
	background-color: $white;
	border: 1px solid $light-gray;
	padding: rem-calc(15);
	& h1, h2, h3, h4, h5, h6 {
		margin: .25em 0 .5em;
	}
	& h1 {
		font-size: rem-calc(32);
	}
	& h2 {
		font-size: rem-calc(22);
	}
	& h3 {
		font-size: rem-calc(20);
	}
	& h4 {
		font-size: rem-calc(19);
	}
    & p {
        line-height: 1.4;
        margin: 1em 0;
        & a {
            text-decoration: underline;
        }
    }
}
.textblock__image-container {
	display: block;
}
.textblock__image {
	display: block;
}
