.header__screen {
	background-color: rgba(#333, 0.7);
	transition: all 1s linear;
	background-repeat: no-repeat;
	position: relative;
	height: 100%;

	&--in-slider {
		padding-top: 100px;

		@include breakpoint(large) {
			padding-top: 200px;
		}
	}

	&--fill-container {
		position: absolute;
		width: 100%;
	}

	&--grayscale {
		backdrop-filter: grayscale(90%);
	}

	&--thin {
		background-color: rgba(#333, 0.3);
	}

	&--with-shadow {
		@include breakpoint(large) {
			background-color: rgba(#333, 0.5);
			background-image: url(../images/shadow.png);
			background-size: cover;
			background-position-x: -600px;
		}


		@include breakpoint(large) {
			background-size: contain;
			background-position-x: -500px;
		}


		@include breakpoint(xlarge) {
			background-size: contain;
			background-position-x: -400px;
		}


		@include breakpoint(xxlarge) {
			background-size: contain;
			background-position-x: -300px;
		}
	}
}

.header__container {
	position: relative;
	z-index: 10;
	&:after {
		transition: background-color 1s;
		background-color: $light-gray;
		display: block;
		width: calc(100% - 1px);
		height: 1px;
		position: absolute;
		bottom: 0;
		left: 0;
		content: "";
	}

	&--dark-header {
        background: $black;
        &:after {
            display: none;
        }
	}

	&--overlay {
		background: linear-gradient(180deg,rgba(0,0,0,.4),transparent);
        z-index: 101;
        &:after {
            transition: background-color 1s;
            background-color: $light-gray;
            display: block;
            width: calc(100% - 1px);
            height: 1px;
            position: absolute;
            bottom: 0;
            left: 0;
            content: "";
        }
	}

	&--in-slider {
		@extend .header__container--overlay;

		position: absolute;
		left: 50%;
		transform: translate(-50%, 0);
		width: 100%;
		z-index: 9999;
	}

	@include breakpoint(large) {
		padding-bottom: 0;

		&:after {
			background-color: transparent;
		}
	}
}

.header {
	position: relative;
	overflow: hidden;
	transition: max-height 0.2s linear;
	&__site-title {
        font-family: $header-font-family;
		font-size: rem-calc(22);
		font-size: calc(22px + 24 * ((100vw - 320px) / (1024 - 320)));
		font-weight: 600;
		letter-spacing: 1px;
		margin: auto;
		padding: rem-calc(18);
		text-align: center;
		text-decoration: none;
		text-transform: uppercase;
		transition: opacity 0.3s linear;
		a {
			color: $body-font-color;
		}
		&:hover {
			text-decoration: none;
		}
		@include breakpoint(large) {
			font-size: rem-calc(46);
		}
	}
	&--dark-header {
		.header__site-title {
			a {
				color: white;
				text-shadow: 0.5px 0.866px 1px rgba(0, 0, 0, 0.39);
			}
		}
	}

	&--full{
		.header__site-title {
			@include breakpoint(large) {

				margin-bottom: 0;
			}
		}
	}
	@include breakpoint(large) {
		max-height: none;
		overflow: visible;
		padding-bottom: 0;
	}
}
.nav-collapse.open {
	max-height: 2500px;
}


// Skip Link
a.skip-link {
    display: block;
	text-align: center;
    background: $light-gray;
    padding: 10px;
    font-size: rem-calc(16);
}
